.our-instagram {
    position: relative;
    &__count {
        font-size: px-to-rem(20px); }
    &__title {
        margin-bottom: px-to-rem(80px); }
    &-photo {
        position: relative;
        max-width: 470px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 25px;
        &:hover .our-instagram-photo-hover {
            visibility: visible;
            opacity: 1; }
        &-hover {
            visibility: hidden;
            position: absolute;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: rgba($pink, .8);
            height: 100%;
            width: 100%;
            text-align: center;
            color: $white;
            opacity: 0;
            transition: .5s;

            &__wrap {
                max-width: 340px; }
            &__text {
                margin-bottom: 20px; }
            & * button {
                background-color: transparent;
                border: 0;
                padding-right: 25px;
                position: relative; } } }
    &-block {
        max-width: px-to-rem(1500px);
        margin: auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around; }

    &__button-show-more {
        margin: auto;
        margin-top: px-to-rem(50px);
        margin-bottom: px-to-rem(80px); } }
.button-show-more {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: px-to-rem(315px);
    padding: 20px 45px;
    width: 100%;
    border-radius: 2px;
    color: $white;
    text-decoration: none;
    background-color: $pink;
    font-size: px-to-rem(28px); }
