.footer {
    background-color: #232323;
    color: $white;
    & * a {
        color: $white;
        text-decoration: none; }
    &__block {
        display: flex;
        justify-content: space-around;
        padding-top: px-to-rem(110px);
        width: 70%; }
    &__title-contacts {
        display: block;
        margin-bottom: 5px; }
    &-menu {
        list-style: none;
        &__item {
            margin-bottom: 5px;
            & * i {
                margin-right: 5px; } } }
    & * .block-icons * i {
        font-size: 16px; }
    & * .block-icons > a {
        width: 25px;
        height: 25px; }
    &__location {
        display: flex;
        &-text {
            display: block;
            padding-left: 5px; } }
    &__undertext {
        display: flex;
        justify-content: space-between;
        width: 95%;
        margin: auto;
        margin-top: px-to-rem(80px);
        padding-bottom: 20px; } }
.footer__row:nth-child(4) > .block-icons {
    display: none; }
.footer * .block-icons {
    justify-content: center; }
